.node {
  width: 100%;
  height: 5%;
  outline: 1px solid rgb(205, 205, 225);
}

.node-start {
  background-color: green;
}

.node-end {
  background-color: red;
}

.node-visited {
  background-color: blue;
}

.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.5);
    background-color: rgba(255, 0, 238, 0.75);
  }

  50% {
    transform: scale(1.5);
    background-color: rgba(0, 60, 255, 0.75);
  }

  25% {
    transform: scale(1.25);
    background-color: rgba(50, 0, 255, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: rgba(0, 2555, 255, 0.75);
  }
}

.node-wall {
  background-color: rgb(12, 53, 71);
}

.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 195, 106);
  }

  100% {
    transform: scale(1);
    background-color: rgb(255, 113, 52);
  }
}
